'use client'

import { useState, useEffect } from 'react'
import { useRouter, useSearchParams } from 'next/navigation'
import { Button } from 'components/ui/button'
import { Input } from 'components/ui/input'
import { Label } from 'components/ui/label'

export default function TravellerDetailsPage() {
  const router = useRouter()
  const searchParams = useSearchParams()

  const bookingIdParam = searchParams.get('bookingId') || ''
  const airlineParam = searchParams.get('airline') || ''
  const flightNumberParam = searchParams.get('flightNumber') || ''
  const fromParam = searchParams.get('from') || ''
  const toParam = searchParams.get('to') || ''
  const departDateParam = searchParams.get('departDate') || ''
  const departTimeParam = searchParams.get('departTime') || ''
  const arriveTimeParam = searchParams.get('arriveTime') || ''
  const priceParam = searchParams.get('price') || ''
  const tripTypeParam = searchParams.get('tripType') || ''

  const [travellers, setTravellers] = useState([
    { fullName: '', age: '', gender: '', passportNumber: '', nationality: '', bookingId: bookingIdParam }
  ])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)

  interface Traveller {
    fullName: string
    age: string
    gender: string
    passportNumber: string
    nationality: string
    bookingId: string
  }

  const handleTravellerChange = (index: number, field: keyof Traveller, value: string) => {
    const updatedTravellers = [...travellers]
    updatedTravellers[index][field] = value
    setTravellers(updatedTravellers)
  }

  const addTraveller = () => {
    setTravellers(prev => [...prev, { fullName: '', age: '', gender: '', passportNumber: '', nationality: '', bookingId: bookingIdParam }])
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')

    // Basic validation: ensure required fields exist for each traveller
    for (let t of travellers) {
      if (!t.fullName || !t.age || !t.gender || !t.bookingId) {
        setError('Please fill in all required fields (Full Name, Age, Gender, and Booking ID) for each traveller.')
        setLoading(false)
        return
      }
    }
    
    try {
      const response = await fetch('/api/booking/traveller', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(travellers),
      })
      const data = await response.json()
      if (data.success) {
        setSuccess(true)
        // Redirect or show success message
        setTimeout(() => {
          router.push('/manage')
        }, 2000)
      } else {
        setError(data.error || 'Submission failed')
      }
    } catch (err) {
      console.error(err)
      setError('An error occurred. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 flex flex-col items-center justify-center p-4" 
         style={{ backgroundImage: "url('https://images.pexels.com/photos/3184432/pexels-photo-3184432.jpeg')", backgroundSize: 'cover' }}>
      <div className="bg-white bg-opacity-90 rounded-lg p-8 max-w-2xl w-full shadow-lg">
        <h1 className="text-3xl font-bold mb-6 text-center">Traveller Details</h1>
        {error && <div className="mb-4 text-red-600">{error}</div>}
        {success && <div className="mb-4 text-green-600">Traveller details saved successfully!</div>}
        <form onSubmit={handleSubmit} className="space-y-6">
          {travellers.map((traveller, index) => (
            <div key={index} className="border p-4 rounded-lg">
              <h2 className="text-xl font-semibold mb-4">Traveller {index + 1}</h2>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor={`fullName-${index}`}>Full Name*</Label>
                  <Input 
                    id={`fullName-${index}`}
                    type="text"
                    value={traveller.fullName}
                    onChange={(e) => handleTravellerChange(index, 'fullName', e.target.value)}
                    placeholder="e.g., John Doe"
                  />
                </div>
                <div>
                  <Label htmlFor={`age-${index}`}>Age*</Label>
                  <Input 
                    id={`age-${index}`}
                    type="number"
                    value={traveller.age}
                    onChange={(e) => handleTravellerChange(index, 'age', e.target.value)}
                    placeholder="e.g., 30"
                  />
                </div>
                <div>
                  <Label htmlFor={`gender-${index}`}>Gender*</Label>
                  <select
                    id={`gender-${index}`}
                    value={traveller.gender}
                    onChange={(e) => handleTravellerChange(index, 'gender', e.target.value)}
                    className="px-3 py-2 border border-slate-300 rounded-md text-sm"
                  >
                    <option value="">Select Gender</option>
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                    <option value="Other">Other</option>
                  </select>
                </div>
                <div>
                  <Label htmlFor={`passportNumber-${index}`}>Passport Number</Label>
                  <Input 
                    id={`passportNumber-${index}`}
                    type="text"
                    value={traveller.passportNumber}
                    onChange={(e) => handleTravellerChange(index, 'passportNumber', e.target.value)}
                    placeholder="Optional"
                  />
                </div>
                <div>
                  <Label htmlFor={`nationality-${index}`}>Nationality</Label>
                  <Input 
                    id={`nationality-${index}`}
                    type="text"
                    value={traveller.nationality}
                    onChange={(e) => handleTravellerChange(index, 'nationality', e.target.value)}
                    placeholder="e.g., Indian"
                  />
                </div>
                <div>
                  <Label htmlFor={`bookingId-${index}`}>Booking ID*</Label>
                  <Input 
                    id={`bookingId-${index}`}
                    type="text"
                    value={traveller.bookingId}
                    onChange={(e) => handleTravellerChange(index, 'bookingId', e.target.value)}
                    placeholder="Enter associated booking ID"
                  />
                </div>
              </div>
            </div>
          ))}
          <Button type="button" variant="outline" onClick={addTraveller}>
            Add Another Traveller
          </Button>
          <Button type="submit" disabled={loading} className="w-full bg-black hover:bg-slate-800 text-white mt-6">
            {loading ? 'Saving Traveller Details...' : 'Submit Traveller Details'}
          </Button>
        </form>
      </div>
    </div>
  )
}
